<template>
  <div>
    <div class="home">
      <div class="block">
        <el-carousel trigger="click"
                     height="400px">
          <el-carousel-item v-for="(item,i) in bannerList"
                            :key="i">
            <img :src="item"
                 height="100%"
                 width="100%">
          </el-carousel-item>
        </el-carousel>
      </div>
      <List :tableData="tableData" :Operation='Operation'>
      </List>
      <div class="block">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="[8, 16, 32, 40]"
                       :page-size="pageSize"
                       small
                       layout=" sizes, prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import List from '@/components/List.vue'
export default {
  components: {
    List
  },
  data () {
    return {
      bannerList: [
        require('@/assets/001.jpg'),
        require('@/assets/002.jpg')
      ],
      tableData: [],
      Operation: false,
      currentPage: 1,
      pageSize: 20,
      total: 0
    }
  },
  watch: {
    $route: 'getAllContents'
  },
  created () {
    this.getAllContents()
  },
  methods: {
    handleSizeChange (val) {
      this.pageSize = val
      this.getAllContents()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getAllContents()
    },
    async getAllContents () {
      const res = await this.$http.get(`/allContents?page=${this.currentPage}&size=${this.pageSize}`)
      this.tableData = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.home {
  max-width: 1200px;
  margin: 0 auto;
}
.block {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}
</style>
